import React, { useState } from 'react';
import { 
  CodeBracketIcon,
  StarIcon,
  EyeIcon,
  ArrowTopRightOnSquareIcon,
  PlusIcon
} from '@heroicons/react/24/outline';

const mockRepositories = [
  {
    id: 1,
    name: 'web-app',
    full_name: 'user/web-app',
    description: 'Modern web application built with React and Node.js',
    private: false,
    html_url: 'https://github.com/user/web-app',
    language: 'JavaScript',
    has_dockerfile: true,
    has_docker_compose: true,
    last_commit_sha: 'abc123',
    last_commit_message: 'Update homepage design',
    last_commit_date: '2024-01-15T10:30:00Z',
    stargazers_count: 15,
    watchers_count: 8
  },
  {
    id: 2,
    name: 'api-service',
    full_name: 'user/api-service',
    description: 'RESTful API service with authentication',
    private: true,
    html_url: 'https://github.com/user/api-service',
    language: 'TypeScript',
    has_dockerfile: true,
    has_docker_compose: false,
    last_commit_sha: 'def456',
    last_commit_message: 'Fix authentication bug',
    last_commit_date: '2024-01-15T09:15:00Z',
    stargazers_count: 8,
    watchers_count: 3
  }
];

const GitHub = () => {
  const [repositories, setRepositories] = useState(mockRepositories);

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">GitHub Integration</h1>
          <p className="text-gray-600">Manage your GitHub repositories and deployments</p>
        </div>
        <button className="btn-primary flex items-center gap-2">
          <PlusIcon className="h-5 w-5" />
          Connect Repository
        </button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {repositories.map((repo) => (
          <div key={repo.id} className="card hover:shadow-md transition-shadow">
            <div className="flex items-start justify-between mb-4">
              <div className="flex items-center gap-3">
                <CodeBracketIcon className="h-6 w-6 text-gray-600" />
                <div>
                  <h3 className="font-medium text-gray-900">{repo.name}</h3>
                  <p className="text-sm text-gray-500">{repo.full_name}</p>
                </div>
              </div>
              {repo.private && (
                <span className="px-2 py-1 text-xs font-medium bg-gray-100 text-gray-800 rounded-full">
                  Private
                </span>
              )}
            </div>

            <p className="text-sm text-gray-600 mb-4">{repo.description}</p>

            <div className="space-y-2 mb-4">
              <div className="flex items-center justify-between text-sm">
                <span className="text-gray-500">Language:</span>
                <span className="font-medium">{repo.language}</span>
              </div>
              <div className="flex items-center justify-between text-sm">
                <span className="text-gray-500">Dockerfile:</span>
                <span className={repo.has_dockerfile ? 'text-green-600' : 'text-red-600'}>
                  {repo.has_dockerfile ? 'Yes' : 'No'}
                </span>
              </div>
              <div className="flex items-center justify-between text-sm">
                <span className="text-gray-500">Docker Compose:</span>
                <span className={repo.has_docker_compose ? 'text-green-600' : 'text-red-600'}>
                  {repo.has_docker_compose ? 'Yes' : 'No'}
                </span>
              </div>
            </div>

            <div className="flex items-center justify-between text-sm text-gray-500 mb-4">
              <div className="flex items-center gap-4">
                <div className="flex items-center gap-1">
                  <StarIcon className="h-4 w-4" />
                  {repo.stargazers_count}
                </div>
                <div className="flex items-center gap-1">
                  <EyeIcon className="h-4 w-4" />
                  {repo.watchers_count}
                </div>
              </div>
              <div className="text-xs">
                {new Date(repo.last_commit_date).toLocaleDateString()}
              </div>
            </div>

            <div className="flex gap-2">
              <button className="flex-1 btn-primary text-sm">
                Deploy
              </button>
              <button className="btn-secondary text-sm">
                <ArrowTopRightOnSquareIcon className="h-4 w-4" />
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default GitHub; 