import React, { useState, useEffect } from 'react';
import { useQuery } from 'react-query';
import { 
  PlayIcon, 
  StopIcon, 
  ArrowPathIcon,
  TrashIcon,
  PlusIcon,
  ServerIcon,
  CpuChipIcon,
  CircleStackIcon,
  GlobeAltIcon
} from '@heroicons/react/24/outline';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, PieChart, Pie, Cell } from 'recharts';
import axios from 'axios';
import toast from 'react-hot-toast';

// Mock data for demonstration
const mockContainers = [
  {
    id: '1',
    name: 'web-app',
    image: 'nginx:latest',
    status: 'running',
    cpu_usage: 15.2,
    memory_usage: 256,
    memory_limit: 512,
    port: 8080,
    domain: 'app.example.com',
    created_at: '2024-01-15T10:30:00Z'
  },
  {
    id: '2',
    name: 'database',
    image: 'postgres:15',
    status: 'running',
    cpu_usage: 8.5,
    memory_usage: 512,
    memory_limit: 1024,
    port: 5432,
    domain: null,
    created_at: '2024-01-15T10:25:00Z'
  },
  {
    id: '3',
    name: 'redis-cache',
    image: 'redis:7-alpine',
    status: 'stopped',
    cpu_usage: 0,
    memory_usage: 0,
    memory_limit: 256,
    port: 6379,
    domain: null,
    created_at: '2024-01-15T10:20:00Z'
  }
];

const mockStats = {
  total_containers: 3,
  running_containers: 2,
  stopped_containers: 1,
  total_cpu_usage: 23.7,
  total_memory_usage: 768,
  total_memory_limit: 1792,
  active_domains: 1
};

const mockChartData = [
  { time: '00:00', cpu: 12, memory: 45 },
  { time: '04:00', cpu: 18, memory: 52 },
  { time: '08:00', cpu: 25, memory: 68 },
  { time: '12:00', cpu: 32, memory: 75 },
  { time: '16:00', cpu: 28, memory: 71 },
  { time: '20:00', cpu: 22, memory: 58 },
  { time: '24:00', cpu: 15, memory: 48 }
];

const statusColors = {
  running: 'bg-green-100 text-green-800',
  stopped: 'bg-red-100 text-red-800',
  paused: 'bg-yellow-100 text-yellow-800',
  restarting: 'bg-blue-100 text-blue-800'
};

const Dashboard = () => {
  const [containers, setContainers] = useState(mockContainers);
  const [stats, setStats] = useState(mockStats);

  const handleContainerAction = async (containerId, action) => {
    try {
      // In a real app, this would call the API
      toast.success(`Container ${action} initiated`);
      
      // Update local state for demo
      setContainers(prev => prev.map(container => {
        if (container.id === containerId) {
          if (action === 'start') {
            return { ...container, status: 'running' };
          } else if (action === 'stop') {
            return { ...container, status: 'stopped', cpu_usage: 0, memory_usage: 0 };
          }
        }
        return container;
      }));
    } catch (error) {
      toast.error(`Failed to ${action} container`);
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'running':
        return <div className="w-2 h-2 bg-green-500 rounded-full"></div>;
      case 'stopped':
        return <div className="w-2 h-2 bg-red-500 rounded-full"></div>;
      case 'paused':
        return <div className="w-2 h-2 bg-yellow-500 rounded-full"></div>;
      default:
        return <div className="w-2 h-2 bg-gray-500 rounded-full"></div>;
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Dashboard</h1>
          <p className="text-gray-600">Overview of your Docker containers and system resources</p>
        </div>
        <button className="btn-primary flex items-center gap-2">
          <PlusIcon className="h-5 w-5" />
          Add Container
        </button>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <div className="card">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <ServerIcon className="h-8 w-8 text-primary-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Total Containers</p>
              <p className="text-2xl font-semibold text-gray-900">{stats.total_containers}</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <div className="h-8 w-8 bg-green-100 rounded-lg flex items-center justify-center">
                <PlayIcon className="h-5 w-5 text-green-600" />
              </div>
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Running</p>
              <p className="text-2xl font-semibold text-gray-900">{stats.running_containers}</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <CpuChipIcon className="h-8 w-8 text-blue-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">CPU Usage</p>
              <p className="text-2xl font-semibold text-gray-900">{stats.total_cpu_usage}%</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <CircleStackIcon className="h-8 w-8 text-purple-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Memory Usage</p>
              <p className="text-2xl font-semibold text-gray-900">
                {Math.round((stats.total_memory_usage / stats.total_memory_limit) * 100)}%
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Charts */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div className="card">
          <h3 className="text-lg font-medium text-gray-900 mb-4">Resource Usage (24h)</h3>
          <ResponsiveContainer width="100%" height={300}>
            <LineChart data={mockChartData}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="time" />
              <YAxis />
              <Tooltip />
              <Line type="monotone" dataKey="cpu" stroke="#3b82f6" strokeWidth={2} name="CPU %" />
              <Line type="monotone" dataKey="memory" stroke="#8b5cf6" strokeWidth={2} name="Memory %" />
            </LineChart>
          </ResponsiveContainer>
        </div>

        <div className="card">
          <h3 className="text-lg font-medium text-gray-900 mb-4">Container Status</h3>
          <ResponsiveContainer width="100%" height={300}>
            <PieChart>
              <Pie
                data={[
                  { name: 'Running', value: stats.running_containers, color: '#10b981' },
                  { name: 'Stopped', value: stats.stopped_containers, color: '#ef4444' }
                ]}
                cx="50%"
                cy="50%"
                outerRadius={80}
                dataKey="value"
                label={({ name, value }) => `${name}: ${value}`}
              >
                {[
                  { name: 'Running', value: stats.running_containers, color: '#10b981' },
                  { name: 'Stopped', value: stats.stopped_containers, color: '#ef4444' }
                ].map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={entry.color} />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </ResponsiveContainer>
        </div>
      </div>

      {/* Container Cards */}
      <div className="card">
        <div className="flex justify-between items-center mb-6">
          <h3 className="text-lg font-medium text-gray-900">Containers</h3>
          <div className="flex gap-2">
            <button className="btn-secondary text-sm">Refresh</button>
            <button className="btn-primary text-sm">Add New</button>
          </div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {containers.map((container) => (
            <div key={container.id} className="bg-white border border-gray-200 rounded-lg p-6 hover:shadow-md transition-shadow">
              <div className="flex items-start justify-between mb-4">
                <div className="flex items-center gap-3">
                  {getStatusIcon(container.status)}
                  <div>
                    <h4 className="font-medium text-gray-900">{container.name}</h4>
                    <p className="text-sm text-gray-500">{container.image}</p>
                  </div>
                </div>
                <span className={`px-2 py-1 text-xs font-medium rounded-full ${statusColors[container.status]}`}>
                  {container.status}
                </span>
              </div>

              <div className="space-y-3 mb-4">
                <div className="flex justify-between text-sm">
                  <span className="text-gray-500">CPU:</span>
                  <span className="font-medium">{container.cpu_usage}%</span>
                </div>
                <div className="flex justify-between text-sm">
                  <span className="text-gray-500">Memory:</span>
                  <span className="font-medium">
                    {container.memory_usage}MB / {container.memory_limit}MB
                  </span>
                </div>
                {container.domain && (
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-500">Domain:</span>
                    <span className="font-medium text-primary-600">{container.domain}</span>
                  </div>
                )}
              </div>

              <div className="flex gap-2">
                {container.status === 'running' ? (
                  <button
                    onClick={() => handleContainerAction(container.id, 'stop')}
                    className="flex-1 btn-secondary text-sm flex items-center justify-center gap-1"
                  >
                    <StopIcon className="h-4 w-4" />
                    Stop
                  </button>
                ) : (
                  <button
                    onClick={() => handleContainerAction(container.id, 'start')}
                    className="flex-1 btn-primary text-sm flex items-center justify-center gap-1"
                  >
                    <PlayIcon className="h-4 w-4" />
                    Start
                  </button>
                )}
                <button
                  onClick={() => handleContainerAction(container.id, 'restart')}
                  className="btn-secondary text-sm flex items-center justify-center gap-1"
                >
                  <ArrowPathIcon className="h-4 w-4" />
                </button>
                <button
                  onClick={() => handleContainerAction(container.id, 'delete')}
                  className="btn-danger text-sm flex items-center justify-center gap-1"
                >
                  <TrashIcon className="h-4 w-4" />
                </button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Dashboard; 